<template>
  <div class="login text-center ">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <div class="mb-5" style="position: relative">
            <div class="logo">
              <v-img
                  :src="require('@/assets/logo-white.png')"
                  contain
                  height="176"
                  class="mb-5"
              />
            </div>
          </div>

          <v-select
              v-model="form.identity_document_id"
              :items="items.documentTypes"
              item-text="name"
              item-value="id"
              label="Tipo de documento"
              filled
              background-color="white"
          ></v-select>

          <v-text-field
              v-model="form.identity_document_number"
              label="Numero de documento"
              filled
              background-color="white"
          ></v-text-field>

          <v-select
              v-model="form.ubigeo_department_id"
              :items="items.ubigeoDepartments"
              item-text="name"
              item-value="id"
              label="Departamento"
              filled
              background-color="white"
          ></v-select>

          <v-text-field
              v-model="form.name"
              label="Nombres"
              filled
              background-color="white"
          ></v-text-field>

          <v-text-field
              v-model="form.last_name"
              label="Apellidos"
              filled
              background-color="white"
          ></v-text-field>

          <v-text-field
              v-model="form.email"
              label="Correo"
              filled
              background-color="white"
          ></v-text-field>

          <v-text-field
              v-model="form.password"
              type="password"
              label="Contraseña"
              filled
              background-color="white"
              hide-details
          ></v-text-field>

          <v-checkbox
              v-model="form.terms"
              color="white"
          >
            <template v-slot:label>
              <div style="color:white">
                He leído y acepto los
                <v-dialog v-model="dialog">
                  <template v-slot:activator="{ on }">
                    <a
                        style="color:white; font-weight: bold"
                        @click.stop
                        v-on="on"
                    >
                      términos y condiciones de uso
                    </a>
                  </template>
                  <v-card>
                    <v-card-text v-html="legal"></v-card-text>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn class="mb-5" block @click="dialog = false"> Cerrar</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </div>
            </template>
          </v-checkbox>

          <v-btn :loading="isLoading" @click="onSave" class="mb-3" block>Regístrarme</v-btn>

          <v-btn @click="onBack" class="mb-5" block>Ya tengo una cuenta</v-btn>
        </v-col>
      </v-row>
    </v-container>
    <svg width="100%" height="124" viewBox="0 0 100 100" preserveAspectRatio="none">
      <defs>
        <polygon id="r1" points="0,0 100,0 70.8,100 0,22.5"/>
        <linearGradient id="g1" x1="0%" y1="0%" x2="100%" y2="0%">
          <stop stop-color="#0084CA" offset="0%"/>
          <stop stop-color="#00406C" offset="100%"/>
        </linearGradient>
      </defs>
      <use x="0" y="0" xlink:href="#r1" fill="url(#g1)"/>
    </svg>
  </div>
</template>
<script>
import axios from 'axios';

export default {
  name: 'Register',
  inject: ['notyf'],
  data: () => ({
    isLoading: false,
    dialog: false,
    avatar: {
      image: '',
      url: '',
    },
    form: {
      identity_document_id: '',
      identity_document_number: '',
      ubigeo_department_id: '',
      name: '',
      last_name: '',
      email: '',
      password: '',
      terms: '',
    },
    items: {
      documentTypes: [],
      ubigeoDepartments: [],
    },
    legal: '',
  }),
  mounted() {
    this.loadDocuments()
    this.loadDepartments()
    this.loadLegalData();
  },
  methods: {
    loadDocuments() {
      axios.get('identity-documents').then((response) => {
        this.items.documentTypes = response.data
      })
    },
    loadDepartments() {
      axios.get('ubigeo/departments').then((response) => {
        this.items.ubigeoDepartments = response.data
      })
    },
    loadLegalData() {
      axios.get('settings/4').then((response) => {
        this.legal = response.data.value
      })
    },
    onSave() {
      this.isLoading = true
      let formData = new FormData()
      formData.append('identity_document_id', this.form.identity_document_id)
      formData.append('identity_document_number', this.form.identity_document_number)
      formData.append('ubigeo_department_id', this.form.ubigeo_department_id)
      formData.append('name', this.form.name)
      formData.append('last_name', this.form.last_name)
      formData.append('email', this.form.email)
      formData.append('password', this.form.password)
      formData.append('terms', this.form.terms)

      axios.post('/auth/register', formData, {headers: {'Content-Type': 'multipart/form-data'}}).then((response) => {
        this.$store.dispatch('login', response.data)
      }).catch((error) => {
        if (error.response.status === 422) {
          let errors = Object.values(error.response.data.errors);
          errors.flat().forEach((message) => {
            this.notyf.error(message);
          })

          return;
        }
        this.notyf.error(error.response.data.message);
      }).then(() => {
        this.isLoading = false
      })
    },
    onBack() {
      this.$router.push({name: 'Login'})
    },
  }
}
</script>

<style lang="scss" scoped>
.login {
  color: white;

  .logo {
    padding-top: 10%;
  }

  .title-primary {
    padding: 2rem 0;
  }
}
</style>